import xinput from '../../wxcomponents/common/xinput.vue'

export default {
	created() {
		uni.getStorage({
			key: 'openId',
			success:res=>{
				this.openId = res.data
			}
		})
	},
	data() {
		return {
			openId:'',
			accessToken:'',
			fkey:'',
			skey:'',
			xtype: 'text'
		}
	},
	methods:{
		bindKey(e){
			this.fkey = e.inputValue
		},
		bindSkey(e){
			this.skey = e.inputValue
		},
		onReg(e){
			let reg =/^(?!([a-zA-Z]+|\d+)$)[a-zA-Z\d]{6}$/
			let str = e
			if(str.match(reg) == null){
				uni.showToast({
					title: '密码格式不正确',
					icon:'none'
				})
			} else {
				this.requestUrl()
			}
		},
		bindSubmit(){
			let skey = this.skey
			let fkey = this.fkey
			if( skey == fkey){
				this.onReg(fkey)
				this.onReg(skey)
			} else{
				uni.showToast({
					title: '两次密码输入不一致',
					icon:'none'
				})
			}
		},
		requestUrl(){
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/shopInfo/setPassword',
				method: 'POST',
				data: {
					openid: this.openId,
					password: this.skey
				},
				success: res => {
					console.log(res)
					uni.showToast({
						title: '设置成功'
					})
					setTimeout(()=>{
						uni.reLaunch({
							url: '../index/index'
						})
					},1000)
				},
				fail: err => {
					console.log(err)
				}
			})
		}
	},
	components: {
		xinput
	},
}

